<template>
    <div>
        <el-container>
            <el-header>
                <!--        ---Header        -->
                <Header
                    active-index="/Main"></Header>
            </el-header>
            <el-main>
<!--                            -->
                <el-table
                        :data="dataSelect.slice((currentPage-1)*pageSize,currentPage*pageSize)"
                        :row-class-name="tableRowClassName"
                        style="width: 100%">
                    <el-table-column
                            label=""
                            prop="userLoginName">
                    </el-table-column>
                    <el-table-column
                            label=""
                            prop="userName">
                    </el-table-column>
                    <el-table-column
                            label=""
                            prop="userStatus">
                    </el-table-column>
                    <el-table-column
                            label=""
                            prop="creator">
                    </el-table-column>
                    <el-table-column
                            label=""
                            prop="createTime">
                    </el-table-column>
                    <el-table-column
                            label=""
                            prop="modifier">
                    </el-table-column>
                    <el-table-column
                            label=""
                            prop="updateTime">
                    </el-table-column>
                    <el-table-column
                            width="400 px"
                            align="right">
                        <template slot="header" slot-scope="scope">
                            <el-input
                                    v-on:input="dataSizeChange()"
                                    v-model="search"
                                    size="medium"
                                    placeholder=""/>
                        </template>
                        <template slot-scope="scope">
                            <el-button
                                size="mini"
                                @click="dialogFormVisible = true"
                                type="primary" plain></el-button>
                            <el-button
                                    size="mini"
                                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                            <el-button
                                    size="mini"
                                    type="danger"
                                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
<!--                                -->
                <el-pagination align='center'
                               @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[1,5,10,20]"
                               :page-size="pageSize"
                               layout="total, sizes, prev, pager, next, jumper"
                               :total="dataSelect.length">
                </el-pagination>
<!--                -->
                <el-dialog title="" :visible.sync="dialogFormVisible" width="30%">
                    <el-form :model="form">
                        <el-form-item label="" :label-width="formLabelWidth">
                            <el-input v-model="form.userLoginName" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" :label-width="formLabelWidth">
                            <el-input v-model="form.userName" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-radio-group v-model="form.userStatus">
                                <el-radio label="0"></el-radio>
                                <el-radio label="1"></el-radio>
                                <el-radio label="2"></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogFormVisible = false"> </el-button>
                        <el-button type="primary" @click="submitAddUser('form')"> </el-button>
                    </div>
                </el-dialog>
<!--                -->
                <el-dialog title="" :visible.sync="dialogEditFormVisible" width="30%">
                    <el-form :model="editForm">
                        <el-form-item label="" :label-width="formLabelWidth">
                            <el-input v-model="editForm.userLoginName" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" :label-width="formLabelWidth">
                            <el-input v-model="editForm.userName" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-radio-group v-model="editForm.userStatus">
                                <el-radio label="0"></el-radio>
                                <el-radio label="1"></el-radio>
                                <el-radio label="2"></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="dialogEditFormVisible = false"> </el-button>
                        <el-button type="primary" @click="submitEditUser('editForm')"> </el-button>
                    </div>
                </el-dialog>
            </el-main>
            <el-footer>(  IE9+,FirefoxChrome    )</el-footer>
        </el-container>
    </div>
</template>

<script>
    /***
     *          
     *          tableData
     *          dataSelect
     *          searchinputdataSizeChangedataSelect,
     *
     */
    import Header from "../components/Header";

    export default {
        name: "Main",
        components: {Header},
        data() {
            return {
                // 
                activeIndex: '/Main',
                // 
                dialogFormVisible: false,
                dialogEditFormVisible: false,
                //
                form: {
                    userName: '',
                    userLoginName: '',
                    userStatus: '',
                },
                //
                editForm: {
                    userId: '',
                    userName: '',
                    userLoginName: '',
                    userStatus: '',
                },
                formLabelWidth: '100px',
                //
                tableData: [],
                //
                search: '',
                currentPage: 1,
                pageSize: 10,
                // 
                dataSelect: [],
            }

        },
        created() {
            this.getUserTableData();
        },
        methods: {
            handleSelect(key, keyPath) {
                console.log(key, keyPath);
            },
            //      
            handleEdit(index, row) {
                console.log(index, row)
                this.editForm.userLoginName = row.userLoginName
                this.editForm.userName = row.userName
                if(row.userStatus === '') this.editForm.userStatus = '1'
                else if (row.userStatus === '') this.editForm.userStatus = '2'
                else if (row.userStatus === '') this.editForm.userStatus = '0'
                this.dialogEditFormVisible = true
            },
            //      
            handleDelete(index, row) {
                const _this = this
                console.log(row.userId);
                _this.$confirm('' + row.userLoginName, '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    _this.$axios.get('/user/invalid/' + row.userId, {
                        headers: {
                            "Authorization": _this.$store.getters.getToken
                        }
                    }).then(res => {
                        console.log(res)
                        if(res.data.code === 200) {
                            _this.$message({
                                type: 'success',
                                message: '!'
                            });
                        } else {
                            _this.$message.error('');
                        }
                    })
                }).catch(() => {
                    _this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            // 
            handleSizeChange(val) {
                console.log(` ${val} `);
                this.currentPage = 1;
                this.pageSize = val;
            },
            // 
            handleCurrentChange(val) {
                console.log(`: ${val}`);
                this.currentPage = val;
            },
            //          
            dataSizeChange() {

                this.dataSelect = this.tableData.filter(data => !this.search
                    || data.userName.toLowerCase().includes(this.search.toLowerCase())
                    || data.userLoginName.toLowerCase().includes(this.search.toLowerCase())
                );
                console.log(this.dataSelect)
            },
            //          
            tableRowClassName({row, rowIndex}) {
                if(row.userLoginName === this.$store.getters.getUser.userLoginName) {
                    return 'success-row';
                }
                else return '';
            },
            //      
            getUserTableData() {
                const _this = this
                this.$axios.get('/user/list', {
                    headers: {
                        "Authorization": _this.$store.getters.getToken
                    }
                }).then(res => {
                    _this.tableData = res.data.data;
                    _this.tableData.forEach(function(element) {
                        //console.log(element)
                        if(element.userStatus === 0) {
                            element.userStatus = ''
                        } else if (element.userStatus === 1){
                            element.userStatus = ''
                        } else if (element.userStatus === 2){
                            element.userStatus = ''
                        } else {
                            element.userStatus = ''
                        }
                    });
                    _this.dataSelect = this.tableData
                })
            },
            //      
            submitAddUser(form) {
                const _this = this
                this.$axios.post('/user/add', this.form, {
                    headers: {
                        "Authorization": _this.$store.getters.getToken
                    }
                }).then(res => {
                    console.log(res)
                    if(res.data.code === 200) {
                        this.$message({
                            message: '' + _this.form.userName,
                            type: 'success'
                        });
                    } else {
                        this.$message.error('');
                    }
                    _this.dialogFormVisible = false;
                })
            },
            //      
            submitEditUser(editForm) {
                const _this = this
                console.log(_this.editForm)
                _this.tableData.forEach(function(element) {
                    console.log(element.userLoginName + '/' + _this.editForm.userLoginName)
                    if(element.userLoginName === _this.editForm.userLoginName) {
                        _this.editForm.userId = element.userId;
                    }
                });
                _this.dialogEditFormVisible = false
                this.$axios.post('/user/edit', _this.editForm, {
                    headers: {
                        "Authorization": _this.$store.getters.getToken
                    }
                }).then(res => {
                    console.log(res)
                    if(res.data.code === 200) {
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                    } else {
                        this.$message.error('');
                    }
                    _this.dialogFormVisible = false;
                })
            }
        }
    }
</script>

<style scoped>
    .el-header {
        width: 100%;
        position:absolute;
        left: 0;
        padding: 0;
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 65px;
        top: 0;
    }
    .el-footer {
        bottom: 0;
        width: 100%;
        position:fixed;
        left: 0;
        padding: 0;
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 65px;
        font-size: 10px;
    }
    .el-main {
        width: 100%;
        margin-top: 60px;
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        height: 850px;
    }
    .el-table /deep/ .success-row {
        background: #f0f9eb;
    }
</style>